import {mapMutations,mapState} from 'vuex'
import {adds} from '../../mock/datas/index.js'
export default {
	data() {
		return {
			listTouchStart: 0,
			listTouchDirection: null,
			modalName:null,
			source: 0,
		}
	},
	onShow() {
		// 获取地址
		this.setAdd(adds)
	},
	onLoad(option){
	 //  实际开发中，这个应该是不需要的，这只是修复，未选择地址返回时为空的情况
	 if (this.userAdds.length >0){
		 this.userAdds.some(res=>{
			 if (res.default){
				 this.setPaymentAdds(res)
			 }
		 })
	 }
	 
	},
	computed:{
	  ...mapState({
		  userAdds:state=>state.user.userAdds,
		  addPitch:state=>state.user.addPitch
		  })
	},
	methods: {
		...mapMutations(['setPaymentAdds','setAdd','defAdd']),
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},
		//选择地址
		checkAddress(item){
			if (this.addPitch){
				// 数据添加到，
				this.setPaymentAdds(item)
				// 跳转上一页
				uni.navigateBack()
			}
			
		},
	
		addAddress(type, item){
			// 修改，添加地址
			uni.navigateTo({
				url: `./addInfo?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		delAdds(item){
			this.defAdd(item)
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			console.log(data, type);
		}
	}
}
